#button-container--post-message {
    padding: auto;
}

#button--modal {
    background-color: pink;
    border-color: purple;
    color: rgb(86, 18, 95);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 22px;
    font-style: italic;
    height: 70px;
    margin-top: 80px;
    padding: 0px 13px;
}

#button--modal:hover {
    background-color: rgb(86, 18, 95);
    color: pink;
}

#input-modal {
    background-color: rgb(247, 247, 176);
    height: 350px;
    margin: 20px auto 0px auto;
    padding: 20px;
    width: 400px;
}

.input-modal--input {
    background-color: rgb(247, 247, 176);
    border: 0px;
    margin: auto;
}
.message {
    background-color: rgb(247, 247, 176);
    box-shadow: 5px 10px 7px rgb(51, 51, 51);
    height: 300px;
    margin: auto;
    overflow: auto;
    padding: 12px;
    text-align: left;
    transition: -webkit-transform .25s;
    transition: transform .25s;
    transition: transform .25s, -webkit-transform .25s;
    width: 300px;
    word-wrap: break-word;
}

.message:nth-child(2n) {
    background-color: rgb(220, 245, 244);
    position: relative;
    top:3px;
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
}

.message:nth-child(3n) {
    position: relative;
    top: -3px;
    -webkit-transform: rotate(-3deg);
            transform: rotate(-3deg);
}

.message:nth-child(5n) {
    background-color: rgb(209, 247, 194);
    position: relative;
    top: -3px;
    -webkit-transform: rotate(-1.5deg);
            transform: rotate(-1.5deg);
}

.message:hover {
    box-shadow: 3px 3px 5px rgb(51, 51, 51);
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    z-index: 1;
}

.message__title {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;   
}

.message__sender {
    font-family: 'Courier New', Courier, monospace;
}

.message__receiver {
    border-bottom: 1px dotted black;
    font-family: 'Courier New', Courier, monospace;
}

.message__body {
    font-family: cursive;
}
#alert-temp {
    color: pink;
    font-size: 30px;
}

#home-page__container {
    background: linear-gradient(101deg, #502900, #D2AB6F, #502900, #AA8347, #502900);
    border: 6px groove saddlebrown;
    display: grid;
    grid-gap: 30px;
    grid-template-columns: 1fr 1fr 1fr;
    margin: auto;
    min-height: 90vh;  
    padding-top: 30px;
}

@media screen and (max-width: 1030px) {
    #home-page__container {
        grid-template-columns: 1fr 1fr;
    }

}

@media screen and (max-width: 710px) {
    #home-page__container {
        grid-template-columns: 1fr;
    }
}
.button--loginform {
    background-color: rgba(241, 166, 248, 0.918);
    border: 1px groove rgb(49, 3, 49);
}

.button--loginform:hover {
    background-color: rgba(6, 1, 7, 0.801);
    color: rgb(240, 214, 245);
}
.alert {
    color: rgb(162, 0, 255);
    font-size: 20px;
}
#main {
  text-align: center;
}

#home-page {
  min-height: 100vh;
  padding: 30px;
}

#login-page {
  margin-top: 10%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

